import React from 'react';
import { Typography, Box, Stack } from '@mui/material';

const ExerciseVideos = ( {exerciseVideos, name}) => {
  if(!exerciseVideos.length) return 'Loading...';
  console.log(exerciseVideos[0]);
  return (
    <Box sx={{mt:{lg: '200px', xs:'20px'}}} p="20px">
      <Typography variant='h4' mb="33px">
        Watch <span style={{color:'#ff2625',textTransform:"capitalize"}}>{name} </span>exercise videos
      </Typography>
      <Stack justifyContent="flex-start" flexWrap="wrap" alignItems="center" sx={{
        flexDirection:{ lg:'row' },
        gap:{lg :'110px',xs:'0'}
      }} >
          {exerciseVideos?.slice(0,6).map((item,index)=>(
            <a
              key={index}
              className='exercise-video'
              href={`https://www.youtube.com/watch?v=${item.video.videoId}`}
              target='_blank'
              rel='nonreferrer' 
            >
              
              <img src={item.video.thumbnails[0].url} alt={item.video.title}/>
              <Box>
                <Typography style={{color:'#000',textTransform:"capitalize"}}>{item.video.title}</Typography>
                <Typography style={{color:'#000'}}>{item.video.channelName}</Typography>

              </Box>
              
            </a>
            
          ))}
      </Stack>
    </Box>
  )
}

export default ExerciseVideos