import React from 'react';
import { Typography, Stack, Button } from '@mui/material';

import BodyPartImage from '../assets/icons/body-part.png';
import TargetImage from '../assets/icons/target.png';
import EquipmentImage from '../assets/icons/equipment.png';


const Detail = ({exerciseDetail}) => {
  const { bodyPart, gifUrl, name, target, equipment}=exerciseDetail;
  
  if (!exerciseDetail || Object.keys(exerciseDetail).length === 0) {
    return <Typography>Loading exercise details...</Typography>;
  } 
  const extraDetail = [
    {
      icon: BodyPartImage,
      name: bodyPart,
    },
    {
      icon: TargetImage,
      name: target,
    },
    {
      icon: EquipmentImage,
      name: equipment,
    },
  ];




  return (
    <Stack gap="60px" sx={{flexDirection: {lg:'row'},p:'20px',
    alignItems:'center'}}>
      <img src={gifUrl} alt={name} l oading="lazy" className='details-image'/>
      <Stack sx={{gap:{lg:'35px',xs:'20px'}}}>
      <Typography sx={{ fontSize: { lg: '64px', xs: '30px' } }} fontWeight={700} textTransform="capitalize">
        {name}
      </Typography>
      <Typography sx={{ fontSize: { lg: '24px', xs: '18px' } }} color="#4F4C4C">
          Exercises keep you strong.{' '}
          <span style={{ textTransform: 'capitalize' }}>{name}</span> It is one
          of the best <br /> exercises to target your {target}. It will help you improve your{' '}
          <br /> mood and gain energy.
        </Typography>
        {extraDetail.map((item)=>(
          <Stack key={item.name} direction="row" gap="24px">
            <Button sx={{background:'#fff2db',borderRadius:'50%',width:'100px',height:'100px'}}>
              <img src={item.icon} alt={bodyPart} style={{
                width:'50px',
                height:'50px'
              }}/>
            </Button>
            <Typography mt='40px' textTransform="capitalize" variant='h5'>
              {item.name}
            </Typography>
          </Stack>
        ))}
      </Stack>
    </Stack>
  )
}

export default Detail